const gallery = document.querySelector(".gallery");

const createMarkup = (arr) => arr.map(({ preview, original, description }) => `<li class="gallery-item">
<a class="gallery-link" href="${original}">
  <img
    class="gallery-image"
    src="${preview}"
    alt="${description}"
  />
</a>
</li>`).join("")

const galleryMarkup = createMarkup(images);

